<template>
  <div>
   <home-header></home-header>
   <swiper></swiper>
   <icons></icons>
   <recommend></recommend>
   <weekgo></weekgo>
  </div>
</template>

<script>
import HomeHeader from '@/components/pages/Header'
import Swiper from '@/components/pages/swiper'
import Icons from '@/components/pages/icons'
import Recommend from '@/components/pages/recommend'
import Weekgo from '@/components/pages/weekgo'
// import axios from 'axios'
import { mapState } from 'vuex'

export default {
  name: 'Home',
  data () {
    return {
    }
  },
  components: {
    HomeHeader,
    Swiper,
    Icons,
    Recommend,
    Weekgo
  },
  methods: {
  },
  mounted () {
    this.lastCity = this.city
    // this.getHomeInfo()
  },
  activated () {
    if (this.lastCity !== this.city) {
      this.lastCity = this.city
      // this.getHomeInfo()
    }
  },
  computed: {
    ...mapState(['city'])
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>



